// Outfit - 400 - Regular
@font-face {
    font-family: 'Outfit';
    src: url(../assets/webfonts/Font-face/Outfit-Regular.woff2) format('woff2'),
        url(../assets/webfonts/Font-face/Outfit-Regular.ttf) format('truetype');
    font-weight: 400;
}

// Outfit - 600 - SemiBold
@font-face {
    font-family: 'Outfit';
    src: url(../assets/webfonts/Font-face/Outfit-SemiBold.woff2) format('woff2'),
        url(../assets/webfonts/Font-face/Outfit-SemiBold.ttf) format('truetype');
    font-weight: 600;
}

// Outfit - 700 - Bold
@font-face {
    font-family: 'Outfit';
    src: url(../assets/webfonts/Font-face/Outfit-Bold.woff2) format('woff2'),
        url(../assets/webfonts/Font-face/Outfit-Bold.ttf) format('truetype');
    font-weight: 700;
}

